import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'swiper/css/navigation';
import 'swiper/css/pagination';



// import required modules
import { EffectFade, Navigation, Pagination } from 'swiper/modules';
import { Container } from 'react-bootstrap';

export default function App() {
  return (
    <>
      <Swiper
        spaceBetween={30}
        effect={'fade'}
        navigation={true}
        pagination={{
          clickable: true,
        }}
        modules={[EffectFade, Navigation, Pagination]}
        className="mySwiper mainslider"
      >
        <SwiperSlide   style={{
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback1.png)`,
    backgroundColor:  "white"

  }}>       
        
           <Container>
            <div className="txtbox">
            <h3  >  </h3>
            <p>   </p>
            <button> </button>
            </div>
          </Container>
       </SwiperSlide>
       
     <SwiperSlide   style={{
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback2.png)`,
    backgroundColor:  "white"

  }}>       
        
           <Container>
            <div className="txtbox">
            <h3  style={{ color:'white'}}>   </h3>
            <p style={{ color:'white'}}></p>
            <button style={{ backgroundColor:'#ea462b'}}> </button>
            </div>
          </Container>
       </SwiperSlide>
         <SwiperSlide   style={{
    backgroundImage: `url(${process.env.PUBLIC_URL}/img/miansliderback3.png)`,
    backgroundColor:  "white"

  }}>       
        
           <Container>
            <div className="txtbox">
            <h3>  </h3>
            <p>   </p>
            <button> </button>
            </div>
          </Container>
       </SwiperSlide>
        <SwiperSlide style={{backgroundImage:`url(${process.env.PUBLIC_URL}/img/miansliderback4.png)`}}>       
        
           <Container>
            <div className="txtbox">
            <h3>  </h3>
            <p>,,</p>
            <button> </button>
            </div>
          </Container>
       </SwiperSlide>
      </Swiper>
    </>
  );
}
